function setInstallTime(installTime) {
    // eslint-disable-next-line no-undef
    chrome.storage.local.set({'zbaseInstallTime': installTime});
}

function updateSetInstallTime(installTime) {
    // eslint-disable-next-line no-undef
    chrome.storage.local.get(['zbaseInstallTime'], response => {
        if (response.zbaseInstallTime === undefined) {
            // eslint-disable-next-line no-undef
            chrome.storage.local.set({'zbaseInstallTime': installTime})
        }
    });
}


export {setInstallTime, updateSetInstallTime}
