import {getZbaseConfig} from './ZbaseRequests'

// eslint-disable-next-line no-undef
let language = chrome.i18n.getUILanguage()
// eslint-disable-next-line no-undef
chrome.storage.local.set({'zbaseLanguage': language}).then()

function loadZbaseConfigToStorage(extension_id, zbaseConfigUrl) {
    let requestData = new URLSearchParams()
    requestData.append('extension_id', extension_id)
    getZbaseConfig(zbaseConfigUrl, requestData).then(response => {
        console.log('请求结果')
        console.log(response)
        if (response['code'] === 100000) {
            // eslint-disable-next-line no-undef
            chrome.storage.local.set({'zbaseConfig': response}).then()
        } else {
            console.log('zbase请求的返回值存在问题没，返回值如下：')
            console.log(response)
        }
    })
}

export {loadZbaseConfigToStorage}

